* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.footer {
  background-color: #eaeced;
  display: flex;
  justify-content: space-between;
  padding: 0px 120px 70px 140px;
}

.divItensMenuFooter {
  margin-top: 60px;
  width: 300px;
}

.divItensMenuFooter div {
  margin-bottom: 10px;
  font-weight: 600;
}

.divItensMenuFooter ul {
  list-style: none;
}

.divItensMenuFooter ul li a {
  text-decoration: none;
  color: gray;
  font-size: 12px;
  line-height: 40px;
  transition: 0.45s;
}

.divItensMenuFooter ul li:hover a {
  color: black;
  font-size: 13px;
  transition: 0.45s;
}

.imgMenuItensFooter {
  width: 250px;
  height: 50px;
  display: flex;
  margin-top: 20px;
  justify-content: space-around;
  align-items: center;
}

.divItensMenuFooter button {
  background-color: rgb(69, 141, 224);
  color: white;
  padding: 8px 20px 8px 20px;
  border: none;
  border-radius: 25px;
  cursor: pointer;
  margin-top: 20px;
  margin-left: 3px;
  transition: 0.25s;
}

.divItensMenuFooter button:hover {
  margin-left: 0px;
  transition: 0.25s;
}

.divItensMenuFooter button a {
  color: white;
  text-decoration: none;
  transition: 0.25s;
  font-size: 13px;
}

.divItensMenuFooter button:hover a {
  color: white;
  font-size: 14px;
  text-decoration: none;
  transition: 0.25s;
}

.imgMenuItensFooter img {
  width: 30px;
  cursor: pointer;
}

@media (max-width: 1200px) {
  .footer {
    background-color: #eaeced;
    display: flex;
    align-items: center;
    text-align: center;
    flex-direction: column;
    padding: 0px 20px 90px 20px;
  }

  .containerButton {
    height: 70px;
  }

  .imgMenuItensFooter {
    margin-top: 0px;
  }
}
