* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.elementoBemVindo {
  background-color: rgb(242, 248, 255);
  display: flex;
  margin-top: 80px;
}

.textoBemVindo {
  width: 50%;
  height: 530px;
  padding-top: 40px;
  padding-left: 58px;
  padding-bottom: 20px;
}

.textoBemVindo h1 {
  font-size: 50px;
  color: black;
  line-height: 55px;
  margin-bottom: 40px;
}

.textoBemVindo div {
  color: gray;
  font-weight: 600;
  font-size: 20px;
  line-height: 35px;
}

.textoBemVindo ul {
  padding-left: 33px;
  margin-top: 20px;
  font-size: 20px;
  line-height: 40px;
}

.textoBemVindo ul li {
  color: rgb(69, 141, 224);
}

.textoBemVindo ul span {
  color: gray;
}

.textoBemVindo button {
  background-color: rgb(247, 78, 6);
  width: 300px;
  height: 50px;
  margin-top: 40px;
  font-size: 15px;
  font-weight: bold;
  border-radius: 5px;
  border: none;
  cursor: pointer;
}

.textoBemVindo button a {
  text-decoration: none;
  color: white;
}

.imgCapaMobile {
  width: 60%;
  text-align: end;
  padding-top: 55px;
  padding-right: 58px;
}

.imgCapaMobile img {
  width: 600px;
}

.investir {
  background-color: rgb(80, 47, 110);
  width: 100%;
  max-height: 800px;
  display: flex;
  flex-direction: column;
  display: inline-block;
  padding-top: 60px;
  padding-bottom: 30px;
}

.investir h1 {
  color: white;
  text-align: center;
  font-size: 38px;
  padding-bottom: 20px;
}

.containerInvestir {
  width: 100%;
  height: 250px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.containerInvestir div {
  width: 350px;
  height: 200px;
  background-color: white;
  margin: 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 15px;
}

.containerInvestir div label {
  font-size: 20px;
  text-align: center;
  font-weight: 600;
  width: 250px;
}

.imgInvestir {
  width: 70px;
  padding: 10px;
  border-radius: 10px;
  margin-bottom: 20px;
  background-color: rgb(80, 47, 110);
}

.methodologia {
  background-color: rgb(242, 248, 255);
  max-height: 1200px;
  display: flex;
}

.mulherImg {
  width: 450px;
  height: 450px;
  margin-top: 70px;
  margin-bottom: 70px;
  margin-left: 80px;
  margin-right: 100px;
}

.containerItensMetodologia {
  display: flex;
  flex-direction: column;
  align-items: start;
  margin-top: 70px;
  width: 50%;
  height: 500px;
}

.methodologia h1 {
  width: 450px;
  max-height: 200px;
  margin-bottom: 80px;
  font-weight: 600;
}

.itensMetodologia {
  width: 90%;
}

.itensMetodologia ul {
  margin-left: 30px;
  line-height: 50px;
  font-size: 20px;
  font-weight: 600;
}

.itensMetodologia ul li {
  color: rgb(69, 141, 224);
}

.itensMetodologia ul span {
  color: gray;
}

.aprender {
  height: 1350px;
  background-color: rgb(247, 82, 36);
  cursor: pointer;
}

.aprender h1 {
  color: white;
  text-align: center;
  text-shadow: -1px -1px white, 1px 1px #333;
  padding-top: 80px;
  padding-bottom: 50px;
}

.containerBtnAprender {
  width: 100%;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.containerBtnAprender button {
  font-size: 20px;
  width: 20%;
  padding: 15px 0% 15px 0%;
  font-weight: 600;
  background-color: white;
  border: none;
  border-radius: 5px;
  box-shadow: 0px 5px 5px 1px rgba(0, 0, 0, 0.265);
}

.btnJavaScript {
  margin-right: 20px;
}

.ContainerConteudoAprender {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: white;
}

.sublinhadoH2 {
  display: flex;
  justify-content: center;
  width: 300px;
  padding-top: 50px;
  border-bottom: 5px solid white;
  padding-bottom: 5px;
}

.conteudoCurso {
  display: flex;
  flex-direction: column;
  line-height: 35px;
}

.jsRn {
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding-top: 50px;
}

.jsRn div {
  width: 350px;
  padding-left: 20px;
  line-height: 40px;
}

.construirProjetos {
  width: 90vw;
  margin-left: 90px;
  margin-top: 30px;
}

.construirProjetos li {
  margin-left: 30px;
}

.Missao {
  background-color: rgb(242, 248, 255);
  display: flex;
  justify-content: space-between;
  padding: 75px 60px 75px 60px;
}

.elementoMissaoCompleto {
  width: 550px;
}

.elementoMissaoCompleto h1 {
  padding-bottom: 50px;
}

.descricaoMissao {
  line-height: 30px;
  font-weight: bold;
  color: gray;
  padding-bottom: 30px;
}

.imgMissao {
  max-width: 700px;
  display: flex;
  align-items: center;
}

.containerBtnMissao {
  display: flex;
  justify-content: space-around;
}

.containerBtnMissao button {
  width: 40%;
  height: 60px;
  margin-right: 10px;
  font-weight: bold;
  border: 3px solid rgb(135, 79, 159);
  border-radius: 10px;
}

.imgMissao img {
  width: 40vw;
}

.projetoPortfolio {
  background-color: rgb(69, 141, 224);
  padding: 70px 10px 20px 10px;
}

.tituloProjetoPorfolio {
  text-align: center;
  padding: 0px 150px 0px 150px;
  color: white;
}

.tituloProjetoPorfolio h1 {
  text-shadow: -1px -1px white, 1px 1px #333;
}

.imgProjetoPortfolioContainer {
  display: flex;
  width: 100%;
  padding: 40px 10px 40px 2vw;
}

.cardProjetoPortfolio {
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 20px 15px 20px 15px;
  border: 2px solid rgb(218, 215, 215);
  border-radius: 10px;
  margin-right: 15px;
  width: 40vw;
}

.cardProjetoPortfolio:hover {
  background-color: rgb(250, 248, 245);
  border-color: rgb(90, 41, 103);
}

.cardProjetoPortfolio h3 {
  text-align: center;
}

.cardProjetoPortfolio img {
  width: 25vw;
  padding-bottom: 30px;
}

.exercicios {
  background-color: rgb(242, 248, 255);
  padding: 70px 10px 20px 10px;
}

.tituloH1Execicicos {
  text-align: center;
  padding: 0px 150px 0px 150px;
}

.containerCardsExercicios {
  display: flex;
  width: 100%;
  padding: 40px 10px 40px 30px;
}

.cardExercicios {
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 20px 15px 20px 15px;
  border: 1px solid rgb(128, 128, 131);
  border-radius: 10px;
  margin-right: 30px;
  width: 40vw;
  text-align: center;
}

.cardExercicios img {
  margin-bottom: 20px;
}
.cardExercicios h2 {
  margin-bottom: 20px;
}

.cardExercicios h4 {
  color: gray;
  line-height: 25px;
}

.planos {
  background-color: rgb(92, 92, 133);
  padding: 70px 30px 40px 30px;
  display: flex;
}

.conteudoPlanos {
  width: 50%;
  margin-right: 80px;
  padding-top: 50px;
}

.tituloLaranjaPlanos {
  font-size: 40px;
  font-weight: bold;
  color: orangered;
  text-shadow: -1px -1px white, 1px 1px #333;
}

.valoresPlanos {
  margin-top: 40px;
  font-size: 40px;
  font-weight: bold;
}

.valoresPlanos label {
  font-weight: bold;
  color: orangered;
  text-shadow: -1px -1px white, 1px 1px #333;
  margin-left: 15px;
}

.whatsAppPlanos {
  display: flex;
  align-items: end;
  margin-top: 20px;
  font-weight: bold;
}

.whatsAppPlanos img {
  cursor: pointer;
}

.planoPro {
  background-color: #eaeced;
  padding: 30px;
  border: 2px solid rgb(247, 82, 36);
  border-radius: 10px;
  width: 390px;
}

.tituloPlanoPro {
  text-align: center;
  border-bottom: 2px solid rgb(247, 82, 36);
  margin-bottom: 20px;
  padding-bottom: 10px;
}

.planoPro ul {
  line-height: 40px;
  margin-left: 20px;
  font-weight: bold;
  font-size: 20px;
}

.planoPro ul li {
  color: rgb(247, 82, 36);
}

.planoPro ul div li span {
  color: black;
}

.planoPro ul div {
  margin-bottom: 20px;
  line-height: 25px;
}

.planoPro button {
  background-color: rgb(247, 82, 36);
  width: 100%;
  margin-top: 30px;
  padding: 10px 0px 10px 0px;
  border: 2px solid black;
  border-radius: 7px;
  transition: 0.45s;
}

.planoPro button:hover {
  background-color: white;
  border: 2px solid rgb(247, 82, 36);
  transition: 0.45s;
}

.planoPro a {
  text-decoration: none;
  color: white;
  font-weight: bold;
  font-size: 20px;
  transition: 0.45s;
}

.planoPro button:hover a {
  color: rgb(247, 82, 36);
  transition: 0.45s;
}

@media (max-width: 1100px) {
  .methodologia {
    background-color: rgb(242, 248, 255);
    max-height: 1200px;
    display: flex;
  }

  .mulherImg {
    display: none;
  }

  .containerItensMetodologia {
    display: flex;
    flex-direction: column;
    align-items: start;
    margin: 70px;
    width: 100%;
    height: 100%;
  }

  .methodologia h1 {
    width: 100%;
    max-height: 200px;
    margin-bottom: 50px;
    font-weight: 600;
  }

  .itensMetodologia {
    width: 90%;
  }

  .itensMetodologia ul {
    margin-left: 30px;
    line-height: 50px;
    font-size: 20px;
    font-weight: 600;
    height: 100%;
    width: 100%;
  }

  .itensMetodologia ul li {
    color: rgb(69, 141, 224);
  }

  .itensMetodologia ul span {
    color: gray;
  }

  .Missao {
    padding: 70px 30px 70px 30px;
  }

  .elementoMissaoCompleto {
    width: 100%;
  }

  .imgMissao {
    display: none;
  }
}

@media (max-width: 1200px) {
  .textoBemVindo {
    width: 100%;
  }

  .imgCapaMobile {
    display: none;
  }

  .aprender {
    height: 1400px;
    background-color: rgb(247, 82, 36);
    cursor: pointer;
  }
}

@media (max-width: 970px) {
  .planos {
    padding: 70px 0px 40px 0px;
    display: flex;
    align-items: center;
    flex-direction: column;
    flex-wrap: wrap;
  }

  .conteudoPlanos {
    width: 90%;
    text-align: center;
    margin-right: 0px;
    padding-top: 0px;
  }

  .whatsAppPlanos {
    display: flex;
    align-items: end;
    justify-content: center;
    margin-bottom: 30px;
  }

  .planoPro {
    width: 390px;
  }
}

@media (max-width: 850px) {
  .containerInvestir {
    width: 100%;
    height: 600px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .containerInvestir div {
    width: calc(100% - 40px);
    height: 200px;
    background-color: white;
    margin: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-radius: 15px;
  }

  .tituloProjetoPorfolio {
    text-align: center;
    padding: 0px 50px 0px 50px;
    color: white;
  }

  .imgProjetoPortfolioContainer {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }

  .cardProjetoPortfolio {
    margin-bottom: 15px;
  }

  .containerCardsExercicios {
    display: flex;
    align-items: center;
    flex-direction: column;
    padding: 40px 10px 40px 40px;
  }

  .cardExercicios {
    margin-bottom: 20px;
    width: 100%;
  }

  .tituloH1Execicicos {
    padding: 0px 50px 0px 50px;
  }
}

