* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
}

.logo {
  width: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.nav {
  background-color: white;
  display: flex;
  flex-direction: column;
  border-bottom: 1px solid rgb(214, 208, 208);
  height: 80px;
  position: fixed;
  top: 0;
  width: 100vw;
}

.nav div {
  display: flex;
}

.divImg {
  border-right: 1px solid rgb(214, 208, 208);
  display: flex;
  align-items: center;
  justify-content: center;
  height: 80px;
  width: 80px;
  cursor: pointer;
  position: relative;
}

.nav ul {
  display: flex;
  list-style: none;
  margin-left: 10px;
  align-items: center;
  justify-content: center;
}

.menuBtn ul li a {
  text-decoration: none;
  color: gray;
}

.menuBtn ul li a:hover {
  color: black;
}

/* Esquema da div de opções com li carreira */

.div0 {
  height: 80px;
}

.div0:hover li {
  color: black;
}

/* -------------------------------- */

.nav ul li {
  margin-left: 30px;
  color: gray;
  cursor: pointer;
  font-weight: bold;
}

.nav ul li:hover {
  color: black;
}

.menuBtn {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.li0 img {
  width: 30px;
}

.nav ul div {
  display: flex;
  width: 120px;
  align-items: center;
  justify-content: space-between;
}

/* botão */

.btnIcone button {
  background-color: rgb(69, 141, 224);
  color: white;
  font-weight: bolder;
  padding: 10px;
  border: none;
  border-radius: 25px;
  cursor: pointer;
}

.nav div button:hover {
  background-color: rgb(119, 172, 233);
}

.btnIcone {
  width: 300px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-right: 50px;
  margin-bottom: 20pz;
  position: absolute;
  right: 0;
  top: 1px;
  padding-right: 10px;
}

/* altura do botão criar conta grátis */
.btnIcone button {
  margin-bottom: 20px;
}

.btnIcone img {
  cursor: pointer;
}

/* Div Carreiras */

.carreiras {
  padding-left: 30px;
  background-color: rgb(9, 63, 87);
  width: 100%;
  height: 400px;
  position: absolute;
  top: 80px;
  z-index: 1;
}

.carreiras div {
  display: flex;
  flex-direction: column;
  padding: 30px;
  margin-left: 30px;
  height: 400px;
}

.carreiras div ul {
  display: flex;
  flex-direction: column;
  align-items: start;
  margin-left: 0;
  padding-left: 0;
}

.carreiras ul li a {
  display: flex;
  flex-direction: row;
  color: white;
  list-style: none;
  line-height: 30px;
  text-decoration: none;
}

.carreiras h3 {
  color: rgb(69, 141, 224);
  font-weight: bolder;
  line-height: 50px;
  padding-left: 30px;
}

.carreiras div li:hover a {
  color: rgb(69, 141, 224);
}

/* Login */

#bt_login {
  display: none;
}

.login {
  visibility: hidden;
  position: absolute;
  right: 0px;
  top: 60px;
  background: white;
  border-radius: 5px;
  box-shadow: 0px 5px 5px 0px rgba(0, 0, 0, 0.1);
  width: 250px;
  height: 280px;
  z-index: 2;
}

#bt_login:checked ~ .login {
  visibility: visible;
}

.btnIcone input {
  width: 100%;
  height: 30px;
  border: 1px solid black;
  border-radius: 5px;
  padding: 10px;
  outline: 0;
}

.emailLogin {
  height: 55px;
}

/* .senhaLogin{
  background-color: blue;
  margin-top: 5px;
} */

.btnIcone form {
  display: flex;
  flex-direction: column;
  padding: 20px;
  width: 100%;
}

.btnIcone h4 {
  margin-bottom: 5px;
}

.btnIcone input[type="email"] {
  margin-bottom: 10px;
}

.btnIcone div {
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
}

p {
  font-size: 10px;
  font-style: italic;
  text-align: end;
  padding-top: 5px;
}

.btnIcone button {
  margin-top: 20px;
}

.btnIcone button a {
  color: white;
}

.btnIcone a {
  text-align: center;
  text-decoration: none;
  color: black;
  cursor: pointer;
}

#bt_menu_retratil {
  visibility: hidden;
}

.ulMenu2 {
  visibility: hidden;
}

.retratil {
  visibility: hidden;
}

@media (max-width: 1030px) {
  .logo {
    margin-top: 5px;
  }

  body {
    overflow-x: hidden;
  }

  .ulMenu1 {
    visibility: hidden;
  }

  .retratil {
    visibility: visible;
    z-index: 3;
  }

  .ulMenu2 {
    visibility: visible;
  }

  .logo {
    position: relative;
    left: 30px;
    padding-left: 0px;
  }

  .divImg {
    border-right: none;
    width: 140px;
  }

  .divImg div {
    height: 50px;
    width: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 20px;
    margin-bottom: 5px;
  }

  .divImg div label {
    font-size: 40px;
    color: gray;
    cursor: pointer;
    padding-right: 10px;
  }

  .criarContaGratis {
    display: none;
  }

  .btnIcone {
    width: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 50px;
    position: absolute;
    right: 0;
    top: 20px;
    padding-right: 10px;
  }

  .login {
    top: 50px;
  }

  .retratil {
    position: absolute;
    left: 0;
    right: 0;
    width: 100%;
    max-height: 100vh;
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
  }

  .retratil::-webkit-scrollbar {
    width: 0px;
  }

  .retratil a {
    text-decoration: none;
    color: rgb(175, 171, 171);
    transition: 0.45s;
  }

  .retratil li:hover a {
    transition: 0.45s;
    color: rgb(69, 141, 224);
  }

  .btnFechar {
    width: 300px;
    height: 60px;
    cursor: pointer;
    padding: 5px;
    padding-left: 15px;
    background-color: rgba(8, 49, 67, 0.979);
    border-top: 1px solid rgb(32, 61, 94);
  }

  .btnFechar label {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30px;
    font-size: 50px;
    cursor: pointer;
    color: white;
  }

  .btnFechar h6 {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 110px;
    font-size: 20px;
    color: white;
  }

  .divRetratil {
    width: 300px;
    max-height: 700px;
    border-top: 1px solid rgb(64, 105, 151);
  }

  .divRetratil ul {
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: start;
    color: black;
    margin: 0;
    width: 300px;
    height: 250px;
  }

  .divRetratil ul li {
    background-color: rgba(9, 64, 87, 0.982);
    line-height: 50px;
    width: 100%;
    margin: 0;
    padding-left: 30px;
  }

  .tecRetratil {
    width: 300px;
    height: 1700px;
    display: flex;
    flex-direction: column;
    border-top: 1px solid rgb(64, 105, 151);
  }

  .tecRetrailBtn {
    background-color: rgba(8, 49, 67, 0.979);
    height: 30px;
    width: 300px;
    display: flex;
    padding: 20px;
    align-items: center;
    justify-content: space-between;
    color: rgb(175, 171, 171);
    font-size: 15px;
    cursor: pointer;
    transition: 0.45s;
  }

  .tecRetrailBtn:hover {
    color: rgb(69, 141, 224);
    transition: 0.45s;
  }

  .tecRetrailBtn:hover div {
    background-color: rgb(69, 141, 224);
    transition: 0.45s;
  }

  #tecRet {
    display: none;
  }

  .divSetaCimaBaixo {
    background-color: gray;
    border-radius: 50%;
    padding: 10px;
    display: flex;
    transition: 0.45s;
  }

  /* Terminar o menú, tentar colocar um efeito transition no menu retratil, porém não sabe se é possivel */
}

@media (max-width: 500px) {
  /* Menu retratil */
  .btnFechar {
    width: 100vw;
  }

  .divRetratil {
    width: 100%;
    max-height: 700px;
  }

  .divRetratil ul {
    width: 100vw;
    height: 250px;
  }

  .divRetratil ul li {
    width: 100vw;
  }

  .tecRetratil {
    width: 100vw;
  }

  .tecRetrailBtn {
    width: 100vw;
  }

  #tecRet {
    display: none;
  }

  /* Login */

  #bt_login {
    display: none;
  }

  .login {
    position: absolute;
    right: -10px;
    width: 100vw;
    height: 100vh;
  }

  .login p {
    margin-top: 20px;
    font-size: 13px;
  }

  .btnIcone button {
    padding: 12px;
    border: 1px solid rgb(69, 141, 224);
    font-size: 20px;
  }

  .btnIcone {
    margin-right: 10px;
  }

  .btnIcone label img {
    margin-right: 10px;
  }

  .login .cadastrar {
    color: rgb(3, 3, 3);
    font-weight: bolder;
    font-size: 20px;
    padding: 12px;
    border: 1px solid black;
    border-radius: 25px;
    cursor: pointer;
  }

  #bt_login:checked ~ .login {
    visibility: visible;
  }

  .btnIcone input {
    width: 100%;
    height: 50px;
    border: 1px solid black;
    border-radius: 5px;
    padding: 10px;
    outline: 0;
  }

  .emailLogin {
    margin-top: 30px;
    height: 110px;
  }

  .btnIcone form {
    display: flex;
    flex-direction: column;
    padding: 20px;
    width: 100%;
  }
}
